<template>
  <div class="background-container fixed inset-0 z-0">
    <!-- ‰ΩéË¥®ÈáèÂç†‰ΩçÂõæÔºàÊèêÈ´òÂàùÂßãÂä†ËΩΩÈÄüÂ∫¶Ôºâ -->
    <div 
        v-if="lowQualityPlaceholder"
        class="absolute inset-0 bg-cover bg-center"
        :style="{
        backgroundImage: `url(${lowQualityPlaceholder})`,
        opacity: imageLoaded ? 0 : 1,
        transition: 'opacity 0.5s ease-out'
      }"
    ></div>

    <!-- ‰∏ªËÉåÊôØÂõæ -->
    <div
        class="absolute inset-0 bg-cover bg-center transition-opacity duration-1000"
        :style="{
        backgroundImage: `url(${backgroundImage})`,
        opacity: imageLoaded ? 1 : 0
      }"
    ></div>

    <!-- ËÉåÊôØË¶ÜÁõñÂ±ÇÔºöÁªìÂêàopacity -->
    <div
        class="absolute inset-0"
        :style="{
      backgroundColor: props.overlayColor,
      opacity: props.overlayOpacity
    }"
    ></div>
  </div>
</template>

<script setup>
import { onMounted, ref, defineAsyncComponent } from 'vue';

const props = defineProps({
  backgroundImage: { type: String, required: true },
  overlayColor: { type: String, default: 'rgba(0, 0, 0, 0.5)' },
  overlayOpacity: { type: Number, default: 1 }
});

// ÂõæÁâáÂä†ËΩΩÁä∂ÊÄÅ
const imageLoaded = ref(false);
const lowQualityPlaceholder = ref(''); // ‰ΩéË¥®ÈáèÂç†‰ΩçÂõæ
const loadingTimeout = 5000; // 5ÁßíÂä†ËΩΩË∂ÖÊó∂

// È¢ÑÂä†ËΩΩËÉåÊôØÂõæ - ‰ΩøÁî®requestIdleCallbackÁ°Æ‰øù‰∏çÈòªÂ°û‰∏ªÊ∏≤Êüì
onMounted(() => {
  // ÂØπ‰∫éSVGÔºåÊàë‰ª¨ÂèØ‰ª•ÂàõÂª∫‰∏Ä‰∏™ÁÆÄÂçïÁöÑÂç†‰ΩçËâ≤ÔºåÂõ†‰∏∫SVGÊú¨Ë∫´Â∑≤ÁªèÂæàÂ∞è
  lowQualityPlaceholder.value = 'data:image/svg+xml;base64,';

  // ‰ΩøÁî®requestIdleCallbackÁ°Æ‰øùËÉåÊôØÂõæÂä†ËΩΩ‰∏ç‰ºöÈòªÂ°ûÈ°µÈù¢Ê∏≤Êüì
  const loadImage = () => {
    const img = new Image();
    
    // ËÆæÁΩÆÂä†ËΩΩË∂ÖÊó∂
    const timeoutId = setTimeout(() => {
      // Ë∂ÖÊó∂Âêé‰ªçÁÑ∂ÊòæÁ§∫ËÉåÊôØËâ≤ÔºåÈÅøÂÖçÁôΩÂ±è
      imageLoaded.value = true;
    }, loadingTimeout);
    
    img.onload = () => {
      clearTimeout(timeoutId);
      imageLoaded.value = true;
    };
    
    img.onerror = () => {
      clearTimeout(timeoutId);
      // Âä†ËΩΩÂ§±Ë¥•Êó∂‰πüÊòæÁ§∫ËÉåÊôØËâ≤
      imageLoaded.value = true;
    };
    
    // ËÆæÁΩÆÂõæÁâáÁöÑ‰ºòÂÖàÁ∫ß‰∏∫‰ΩéÔºå‰∏çÂΩ±ÂìçÂÖ≥ÈîÆËµÑÊ∫êÂä†ËΩΩ
    if ('loading' in img) {
      img.loading = 'lazy';
    }
    
    img.src = props.backgroundImage;
  };

  if ('requestIdleCallback' in window) {
    window.requestIdleCallback(loadImage);
  } else {
    // ÈôçÁ∫ßÊñπÊ°àÔºöÂª∂ËøüÂä†ËΩΩÔºåÈÅøÂÖçÈòªÂ°ûÂàùÂßãÊ∏≤Êüì
    setTimeout(loadImage, 200);
  }
});
</script>

<style scoped>
.background-container {
  pointer-events: none;
}

/* Á°Æ‰øùËÉåÊôØÂõæ‰∏ç‰ºöÈòªÊ≠¢È°µÈù¢ÁöÑ‰∫§‰∫í */
.background-container {
  will-change: opacity;
}
</style>

