<template>
  <div class="redirect-notice fixed bottom-4 left-1/2 transform -translate-x-1/2 bg-black bg-opacity-70 text-white px-6 py-3 rounded-full backdrop-blur-sm shadow-lg z-40">
    <p class="text-center text-sm md:text-base">
      {{ message }} {{ secondsRemaining }} {{ secondsLabel }}
      <button
          v-if="allowSkip"
          @click="handleSkip"
      class="text-yellow-300 hover:text-yellow-200 font-medium ml-2 underline"
      >
      {{ skipButtonText }}
      </button>
    </p>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue';

// ÂÆö‰πâ props - ÂÆåÊï¥ÂåπÈÖçApp.vue‰∏≠‰º†ÈÄíÁöÑÂèÇÊï∞
const props = defineProps({
  targetUrl: {
    type: String,
    required: true
  },
  countdownSeconds: {
    type: Number,
    default: 3
  },
  message: {
    type: String,
    default: 'You will be redirected in:'
  },
  secondsLabel: {
    type: String,
    default: 'seconds'
  },
  allowSkip: {
    type: Boolean,
    default: true
  },
  skipButtonText: {
    type: String,
    default: 'Skip ‚Üí'
  }
});

// ÂÆö‰πâ‰∫ã‰ª∂ÂèëÂ∞ÑÂô®
const emit = defineEmits(['skip', 'auto-redirect']);

const secondsRemaining = ref(props.countdownSeconds);
let countdownTimer = null;

// ÂêØÂä®ÂÄíËÆ°Êó∂
const startCountdown = () => {
  secondsRemaining.value = props.countdownSeconds;

  countdownTimer = setInterval(() => {
    secondsRemaining.value--;

    if (secondsRemaining.value <= 0) {
      clearInterval(countdownTimer);
      performRedirect(); // Áõ¥Êé•ÊâßË°åÈáçÂÆöÂêë
    }
  }, 1000);
};

// ÊâßË°åÈáçÂÆöÂêë
const performRedirect = () => {
  emit('auto-redirect');
  if (props.targetUrl) {
    window.location.href = props.targetUrl;
  }
};

// Â§ÑÁêÜË∑≥ËøáÂÄíËÆ°Êó∂
const handleSkip = () => {
  emit('skip');
  skipCountdown();
};

// Ë∑≥ËøáÂÄíËÆ°Êó∂Ôºà‰æõÁà∂ÁªÑ‰ª∂Ë∞ÉÁî®Ôºâ
const skipCountdown = () => {
  if (countdownTimer) {
    clearInterval(countdownTimer);
  }
  performRedirect();
};

onMounted(() => {
  startCountdown();
});

onUnmounted(() => {
  if (countdownTimer) clearInterval(countdownTimer);
});

// Êö¥Èú≤ÊñπÊ≥ïÁªôÁà∂ÁªÑ‰ª∂
defineExpose({
  reset: startCountdown,
  skipCountdown: skipCountdown // Ê∑ªÂä†skipCountdownÊñπÊ≥ï
});
</script>
